@keyframes float {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-20px);
  }
  100% {
    transform: translateY(0px);
  }
}

@keyframes fadeInOut {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 1;
  }
  75% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes fadeInOut01 {
  0% {
    opacity: 0;
    transform: translateY(0px);
  }
  25% {
    opacity: 1;
  }
  50% {
    transform: translateY(-20px);
  }
  75% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translateY(0px);
  }
}

@keyframes scroll {
  0% {
    top: 25%;
    height: 4px;
  }
  10% {
    height: 8px;
  }
  50% {
    top: 75%;
    height: 4px;
  }
  100% {
    top: 25%;
    height: 4px;
  }
}
@keyframes marquee {
  0% {
    left: 100%;
  }
  100% {
    left: -200%;
  }
}
@keyframes text {
  0% {
    transform: translateY(0px);
  }
  25% {
    transform: translateY(-60px);
  }
  75% {
    transform: translateY(60px);
  }
  100% {
    transform: translateY(0px);
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeData {
  0% {
    opacity: 0;
  }
  5% {
    opacity: 1;
  }
  95% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes dotPulse {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
